/* General page styles */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

/* Header (Navbar) Styling */
#header {
  padding: 15px;
}

.navbar-brand {
  font-size: 24px;
  font-weight: bold;
  color: #007bff;
}

#account {
  font-size: 18px;
  color: #555;
}

/* Ticket container styles */
#bilietai {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

/* Ticket (Bilietas) styles */
.bilietas {
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.bilietas:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.bilietas img {
  width: 100%;
  border-radius: 8px;
  margin-bottom: 15px;
}

.bilietas .card-title {
  font-size: 20px;
  color: #333;
  margin-bottom: 10px;
}

.bilietas .card-text {
  font-size: 18px;
  color: #555;
}

.bilietas .btn {
  background-color: #007bff;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.bilietas .btn:hover {
  background-color: #0056b3;
}

/* Responsive Design */
@media (max-width: 768px) {
  #bilietai {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}

